import React , { Suspense } from 'react';
import { TRANSFERS_ROUTE, OWERVIEW_ROUTE, LOGIN_ROUTE, 
  REGISTRATION_ROUTE, ARCHIVE_ROUTE, TRANSFERS_TOPUP_ROUTE,
  TRANSFERS_SEND_ROUTE, TRANSFERS_WITHDRAW_ROUTE, 
  ACCOUNT_ROUTE, 
  ACCOUNT_PROFILE_ROUTE,
  ACCOUNT_NOTIFICATIONS_ROUTE,
  ACCOUNT_SECURITY_ROUTE, 
  ARCHIVE_TRANSACTIONS_ROUTE,
  ARCHIVE_CHART_ROUTE} from './utils/consts';
import Spinner from './components/Spinner/Spinner';
import { Navigate } from 'react-router-dom';


import Login from './pages/Auth/Login';
const Registration = React.lazy(() => import('./pages/Auth/Registration'));
const Overview = React.lazy(() => import('./pages/Overview/Overview'));
const Archive = React.lazy(() => import('./pages/Archive/Archive'));
const ArchiveTransactions = React.lazy(() => import('./pages/Archive/Pages/ArchiveTransactions/ArchiveTransactions'));
const ArchiveChart = React.lazy(() => import('./pages/Archive/Pages/ArchiveChart/ArchiveChart'));

const Transfers = React.lazy(() => import('./pages/Transfers/Transfers'));
const TopUp = React.lazy(() => import('./pages/Transfers/Transfer/TopUp'));
const Send = React.lazy(() => import('./pages/Transfers/Transfer/Send'));
const Withdraw = React.lazy(() => import('./pages/Transfers/Transfer/Withdraw'));
const Account = React.lazy(() => import('./pages/Account/Account'));
const Profile = React.lazy(() => import('./pages/Account/Pages/Profile/Profile'));
const Notifications = React.lazy(() => import('./pages/Account/Pages/Notifications/Notifications'));
const Security = React.lazy(() => import('./pages/Account/Pages/Security/Security'));


export const authRoutes = [
  {path: TRANSFERS_ROUTE, component: <Suspense fallback={<Spinner animation={"grow"} />}><Transfers /></Suspense>, 
    inner: [
      {path: TRANSFERS_TOPUP_ROUTE, component: <Suspense fallback={<Spinner animation={"grow"} />}><TopUp /></Suspense>},
      {path: TRANSFERS_SEND_ROUTE, component: <Suspense fallback={<Spinner animation={"grow"} />}><Send /></Suspense>},
      {path: TRANSFERS_WITHDRAW_ROUTE, component: <Suspense fallback={<Spinner animation={"grow"} />}><Withdraw /></Suspense>}
    ]
  },
  {path: ARCHIVE_ROUTE, component: <Suspense fallback={<Spinner animation={"grow"} />}><Archive /></Suspense>,
    inner: [
      {path: ARCHIVE_TRANSACTIONS_ROUTE, component: <Suspense fallback={<Spinner animation={"grow"} />}><ArchiveTransactions /></Suspense>},
      {path: ARCHIVE_CHART_ROUTE, component: <Suspense fallback={<Spinner animation={"grow"} />}><ArchiveChart /></Suspense>},

    ]
  },
  {path: ACCOUNT_ROUTE, component: <Suspense fallback={<Spinner animation={"grow"} />}><Account /></Suspense>,
    inner: [
      {path: ACCOUNT_PROFILE_ROUTE, component: <Suspense fallback={<Spinner animation={"grow"} />}><Profile /></Suspense>},
      {path: ACCOUNT_NOTIFICATIONS_ROUTE, component: <Suspense fallback={<Spinner animation={"grow"} />}><Notifications /></Suspense>},
      {path: ACCOUNT_SECURITY_ROUTE, component: <Suspense fallback={<Spinner animation={"grow"} />}><Security /></Suspense>}
    ]
  },
  {path: OWERVIEW_ROUTE, component: <Suspense fallback={<Spinner animation={"grow"} />}><Overview /></Suspense>},
  {path: '*', component: <Navigate to={OWERVIEW_ROUTE} />}
];

export const publicRoutes = [
  {path: LOGIN_ROUTE, component: <Login />},
  {path: REGISTRATION_ROUTE, component: <Suspense fallback={<Spinner animation={"grow"} />}><Registration /></Suspense>},
    {path: '*', component: <Navigate to={LOGIN_ROUTE} />}
];
