import { $authHost } from "./index";

export const getBalance = async (currency = 'usd') => {
  const  { data }  = await $authHost.get('information/balance', { params: { currency } });
  return data.balance;
}

export const getSummary = async (start_date, end_date, currency = 'usd') => {
  const  { data }  = await $authHost.get('information/summary', { params: { start_date, end_date, currency } });
  return data;
}
export const getSeries = async (start_date, end_date, currency = 'usd') => {
  const  { data }  = await $authHost.get('information/series', { params: { start_date, end_date, currency } });
  return data;
}