import React, { useRef, useState, useEffect } from 'react';
import './Navbar.scss';
import userStore from '../../store/UserStore';
import { Link, NavLink } from 'react-router-dom';
import { LOGIN_ROUTE, OWERVIEW_ROUTE, TRANSFERS_TOPUP_ROUTE, TRANSFERS_ROUTE, ARCHIVE_ROUTE,
  ACCOUNT_ROUTE, ACCOUNT_PROFILE_ROUTE, ACCOUNT_SECURITY_ROUTE, ACCOUNT_NOTIFICATIONS_ROUTE, ARCHIVE_TRANSACTIONS_ROUTE } from './../../utils/consts';
import { observer } from 'mobx-react-lite';
import hamburger from '../../img/UI/hamburger.svg';
import Logo from './../Logo/Logo';
import Menu from '../Menu/Menu';
import UI from '../../store/UI';
import { logout } from './../../API/userAPI';
import arrowDown from '../../img/UI/arrowDown.svg';

const Navbar = observer(() => {
  const userMore = useRef(null);
  const [isShow, setIsShow] = useState(false);

  useEffect(() => {
    UI.setDrops([...UI.drops, userMore.current]);
  }, [userStore.currentUser]);

  return (
    <>
      <nav className='navbar'>
        {userStore.currentUser && 
          <button onClick={() => setIsShow(true)} >
            <img src={hamburger} alt="hamburger" className='navbar__hamburger' />
          </button>
        }
        <Logo />
        
        
        {userStore.currentUser && 
          <div className='navbar__center' onClick={() => userMore.current.classList.remove('navbar__userMore-active')}>
            <NavLink to={OWERVIEW_ROUTE} className={({ isActive }) => (isActive 
              ? 'navbar__route navbar__route-active' : 'navbar__route')}>Overview</NavLink>
            <NavLink to={TRANSFERS_ROUTE + "/" + TRANSFERS_TOPUP_ROUTE} className={({ isActive }) => (isActive 
              ? 'navbar__route navbar__route-active' : 'navbar__route')}>Transfers</NavLink>
            <NavLink to={ARCHIVE_ROUTE + '/' + ARCHIVE_TRANSACTIONS_ROUTE} className={({ isActive }) => (isActive 
              ? 'navbar__route navbar__route-active' : 'navbar__route')}>Archive</NavLink>
          </div>
        }

        {userStore.currentUser 
          ?
            <div className='navbar__user navbar__tail'>
              <Link to={ACCOUNT_ROUTE} className='navbar__userImg'>{userStore?.currentUser?.first_name?.charAt(0) + 
                                                                    userStore?.currentUser?.last_name?.charAt(0)}</Link>
              <p className='navbar__userName'>{userStore.currentUser.first_name + ' ' +
                                                                    userStore.currentUser.last_name}</p>
              <button className='navbar__userMoreIcon' onClick={() => userMore.current.classList.toggle('navbar__userMore-active')}>
                <img src={arrowDown} alt='arrowDown' />
              </button>
              <div ref={userMore} className='navbar__userMore' onClick={() => 
                userMore.current.classList.toggle('navbar__userMore-active')}>
                <Link to={ACCOUNT_ROUTE + '/' + ACCOUNT_PROFILE_ROUTE} className='navbar__userLink'>Account</Link>
                <Link to={ACCOUNT_ROUTE + '/' + ACCOUNT_SECURITY_ROUTE} className='navbar__userLink'>Setting</Link>
                <Link to={ACCOUNT_ROUTE + '/' + ACCOUNT_NOTIFICATIONS_ROUTE} className='navbar__userLink'>Notifications</Link>
                <button className='navbar__userLink navbar__userLink-logOut' onClick={async () => {
                  await logout();
                  userStore.setCurrentUser(null);
                  localStorage.removeItem('user');
                  window.location.reload(); // to reset store
                }}>Log out</button>
              </div>            
            </div>
          :       
            <Link to={LOGIN_ROUTE} className='navbar__tail'>
              <div className='navbar__title-tail'>
                Log In
              </div>
              <svg width="19" height="18" viewBox="0 0 19 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M8.56539 13.29C8.95539 13.68 9.58539 13.68 9.97539 13.29L13.5654 9.7C13.9554 9.31 13.9554 8.68 13.5654 8.29L9.97539 4.7C9.58539 4.31 8.95539 4.31 8.56539 4.7C8.17539 5.09 8.17539 5.72 8.56539 6.11L10.4454 8H1.77539C1.22539 8 0.775391 8.45 0.775391 9C0.775391 9.55 1.22539 10 1.77539 10H10.4454L8.56539 11.88C8.17539 12.27 8.18539 12.91 8.56539 13.29ZM16.7754 0H2.77539C1.66539 0 0.775391 0.9 0.775391 2V5C0.775391 5.55 1.22539 6 1.77539 6C2.32539 6 2.77539 5.55 2.77539 5V3C2.77539 2.45 3.22539 2 3.77539 2H15.7754C16.3254 2 16.7754 2.45 16.7754 3V15C16.7754 15.55 16.3254 16 15.7754 16H3.77539C3.22539 16 2.77539 15.55 2.77539 15V13C2.77539 12.45 2.32539 12 1.77539 12C1.22539 12 0.775391 12.45 0.775391 13V16C0.775391 17.1 1.67539 18 2.77539 18H16.7754C17.8754 18 18.7754 17.1 18.7754 16V2C18.7754 0.9 17.8754 0 16.7754 0Z" fill="#1E4DFC"/>
              </svg>
            </Link>
          }
      </nav>
      <Menu isShow={isShow} setShow={setIsShow} />
    </>
  );
});

export default Navbar;