import { makeAutoObservable } from "mobx";

class UserStore {
  constructor() {
    this.currentUser = null;
    this.balance = null;
    this.transactions = null;

    this.series = null;
    this.isFetchingTransactions = true;
    this.summary = null;
    this.currency = '$';

    this.sendedMoney = 0;
    this.limit = 200000;

    makeAutoObservable(this);
  }
  setSendedMoney(sendedMoney) {
    this.sendedMoney = sendedMoney;
  }

  setIsFetchingTransactions(isFetchingTransactions) {
    this.isFetchingTransactions = isFetchingTransactions;
  }
  
  setCurrency(currency) {
    this.currency = currency;
  }

  setSummary(summary) {
    this.summary = summary;
  }

  setSeries(series) {
    this.series = series;
  }

  setTransactions(transactions) {
    this.transactions = transactions;
  }

  setBalance(balance) {
    this.balance = balance;
  }

  setCurrentUser(user) {
    this.currentUser = user;
  }
}

export default new UserStore();

