import { $authHost } from "./index";

export const topup = async (value) => {
  const  { data }  = await $authHost.post('transactions/fill', { value });
  return data;
}

export const withdraw = async (value) => {
  const  { data }  = await $authHost.post('transactions/withdraw', { value });
  return data;
}

export const send = async (email, value) => {
  const  { data }  = await $authHost.post('transactions/pay', { email, value });
  return data;
}

export const getTransactions = async (start_date, end_date) => {
  const  { data }  = await $authHost.get('transactions/', { params: { start_date, end_date} });
  return data.transactions;
}
