import { observer } from 'mobx-react-lite';
import React, { useEffect } from 'react';
import { Routes, Route, useLocation, useNavigate } from 'react-router-dom';
import { authRoutes, publicRoutes } from '../routes';
import UI from '../store/UI';
import userStore from '../store/UserStore';

const AppRouter = observer(() => {
  const location = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    navigate(location.pathname); // when reload to navigate on reloaded page
  },[]);
  
  return (
    <div className='pages' onClick={(e) => {
      UI.drops.forEach(drop => drop?.classList?.remove('navbar__userMore-active'))
    }}>
      <Routes>
        { 
          userStore.currentUser !== null
            ? authRoutes.map((route) => {
              return <Route key={route.path} path={route.path} element={route.component}>
                {route?.inner?.map((innerRoute) => <Route key={innerRoute.path} path={innerRoute.path} element={innerRoute.component} />)}
              </Route>
            })
            : publicRoutes.map((route) => {
              return <Route key={route.path} path={route.path} element={route.component}>
                {route?.inner?.map((innerRoute) => <Route key={innerRoute.path} path={innerRoute.path} element={innerRoute.component} />)}
              </Route>
            })
        }
      </Routes>
    </div>
  );
});


export default AppRouter;


