import React from 'react';
import logo from '../../img/logo.svg';
import { Link } from 'react-router-dom';
import './Logo.scss';

const Logo = () => {
  return (
    <div className='logo'>
      <Link to='/' className='logo__link'>
        <div className='logo__icon'>
          <img src={logo} alt="logo" />
        </div>
      </Link>
      <h1 className='logo__title'>
        Bank for people
      </h1>
    </div>
  );
};

export default Logo;