import { makeAutoObservable } from "mobx";

class FilterStore {
  constructor() {
    this.sorting = false;
    this.transactions = null;
    this.transactionsByDate = null;

    this.categoryFilters = [
      {type: 'payment_fill', state: false},
      {type: 'payment_made', state: false},
      {type: 'payment_withdraw', state: false},
      {type: 'payment_received', state: false}
    ]
    this.from = '';
    this.to = '';
    this.searchValue = '';

    this.filterByAmount = false;
    this.filteredByDate = false;
    this.filteredByCategory = false;
    this.filteredBySearch = false;
    this.isResults = true;
    makeAutoObservable(this);
  }

  setTransactionsByDate(transactionsByDate) {
    this.transactionsByDate = transactionsByDate;
  }

  setIsResults(isResults) {
    this.isResults = isResults;
  }

  setSearchValue(searchValue) {
    this.searchValue = searchValue;
  }

  setSorting(sorting) {
    this.sorting = sorting;
  }

  setTransactions(transactions) {
    this.transactions = transactions;
  }

  setTransactionsMain(transactionsMain) {
    this.transactionsMain = transactionsMain;
  }

  setCategoryFilters (categoryFilters) {
    this.categoryFilters = categoryFilters;
  }

  setFilterByAmount (filterByAmount) {
    this.filterByAmount = filterByAmount;
  }

  setFilteredByDate (filteredByDate) {
    this.filteredByDate = filteredByDate;
  }

  setFilteredByCategory (filteredByCategory) {
    this.filteredByCategory = filteredByCategory;
  }
  setFilteredBySearch(filteredBySearch) {
    this.filteredBySearch = filteredBySearch;
  }
  
  setFrom (from) {
    this.from = from;
  }
  
  setTo (to) {
    this.to = to;
  }
  
}

export default new FilterStore();

