import { makeAutoObservable } from "mobx";

class UI {
  constructor() {
    this.drops = [];
    this.device = 'dekstop';
    makeAutoObservable(this);
  }

  setDevice(device) {
    this.device = device;
  }

  setDrops(drops) {
    this.drops = drops;
  }

}

export default new UI();

