export const validateMoney = (element, value = 0) => {
  if(element.value < value || element.value.startsWith('+')) element.value = null;
}

export const isEmailValid = (email) => {
  let validRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
  return email?.match(validRegex) ? true : false;
}

export const isPasswordValid = (password) => {
  if (password.length < 8) return false; // length
  if (password.search(/\d/) == -1) return false; // number
  if (password.search(/[a-zA-Z]/) == -1) return false; // letters
  return true;
}

export const checkIfNumberIsMoney = (number) => {
  let regex  = /(^\d+(?:\.\d{0,2})$)|(^[1-9]+)/;
  if(number === '0.0') return false;
  if(number === '0.00') return false;
  if (regex.test(number)) return true;
  else return false;
}