import { $host, $authHost } from "./index";

export const registration = async (username, email, password, first_name = '',
                                  last_name = '', birth_date = '') => {
  const  { data }  = await $host.post('user/signup', { email, username, first_name, last_name, birth_date, password });
  return data;
}

export const login = async (username, password) => {
  const params = new URLSearchParams();
  params.append('username', username);
  params.append('password', password);
  const { data } = await $host.post('user/login', params);
  localStorage.setItem('token', data.access_token);
  return data;
}

export const logout = async () => {
  const { data } = await $authHost.post('user/logout');
  localStorage.removeItem('token');
  return data;
}

