import React, { useEffect } from 'react';
import Navbar from './components/Navbar/Navbar';
import AppRouter from './components/AppRouter';
import { BrowserRouter } from 'react-router-dom';
import './App.scss';
import { observer } from 'mobx-react-lite';
import userStore from './store/UserStore';
import { getTransactions } from './API/transactionsAPI';
import { getSeries, getBalance, getSummary } from './API/balanceAPI';
import { getMonthBeforeDate } from './utils/dates';
import UI from './store/UI';
import { MOBILE, UI_DESKTOP, UI_MOBILE } from './utils/consts';
import FilterStore from './store/FilterStore';

const App = observer(() => {

  const fetchTransactions = async () => {
    userStore.setIsFetchingTransactions(true);
    const today = new Date().toLocaleDateString('en-CA'); // convert to format yyyy--mm--dd 
    const transactions = await getTransactions('2020-02-01', today)
    userStore.setTransactions(transactions);
    FilterStore.setTransactions(transactions);
    userStore.setIsFetchingTransactions(false);
  };

  const fetchBalance = async () => {
    const balance = await getBalance();
    userStore.setBalance(balance);
  }

  const fetchSumOfTransactionsByLastMonth = async () => {
    // const [today2, monthLater] = getMonthBeforeDate(1)
    if(userStore.transactions && userStore.transactions.length !== 0) {
      const today = new Date().toLocaleDateString('en-ca');
      const dateOfFirstMadedTransaction = userStore.transactions[0].date;
      userStore.setSummary(await getSummary(dateOfFirstMadedTransaction,today));
    }
  }

  const calculateSumOfSendedMoney = () => {
    const monthBefore = new Date();
    monthBefore.setMonth(monthBefore.getMonth() - 1);
    const monthBeforeFormat = monthBefore.toLocaleDateString('en-CA')
    let sendedMoneySum = 0;
    userStore?.transactions?.filter(transaction => transaction.type === 'payment_made' &&  monthBeforeFormat <= transaction.date)
      .forEach(transaction => sendedMoneySum += +transaction.value)
    userStore.setSendedMoney(sendedMoneySum);
  }

  useEffect(() => {
    fetchSumOfTransactionsByLastMonth();
    calculateSumOfSendedMoney();
  }, [userStore.transactions])

  useEffect(() => {
    if(userStore.balance !== null) {
      fetchTransactions();
    }
  }, [userStore.balance]); // when change balance with transfers like send, topup, withdraw 
                                // we change balance in userStore -> therefore we have to reload transactions archive

  useEffect(() => {
    if(userStore.currentUser !== null) fetchBalance();
  }, [userStore.currentUser]); // when user change we must to fetch all data again for another user

  useEffect(() => {
    const token = localStorage.getItem('token');
    if(token) {
      const user = JSON.parse(localStorage.getItem('user'));
      userStore.setCurrentUser(user);// when reload page if we have token in local storage set user
    }

    // handle resize of win
    const handleResize = () => {
      if(window.innerWidth < MOBILE) UI.setDevice(UI_MOBILE);
      else UI.setDevice(UI_DESKTOP);
    }
    handleResize();
    window.addEventListener('resize', handleResize)
  }, []);

  return (
    <>
      <BrowserRouter>
        <Navbar />
        <AppRouter />
      </BrowserRouter>
    </>
  );
});

export default App;
