import React from 'react';
import Logo from '../Logo/Logo';
import './Menu.scss';

import close from '../../img/UI/close.svg';
import { Link } from 'react-router-dom';
import { OWERVIEW_ROUTE, ARCHIVE_ROUTE, TRANSFERS_ROUTE,
TRANSFERS_TOPUP_ROUTE, TRANSFERS_SEND_ROUTE, TRANSFERS_WITHDRAW_ROUTE, ARCHIVE_TRANSACTIONS_ROUTE } from './../../utils/consts';

const Menu = ({isShow, setShow}) => {
  return (
    <>
      { isShow &&
        <div className='menu'>
          <div className='menu__header'>
            <Logo />
            <button className='menu__close' onClick={() => setShow(false)}>
              <img src={close} alt="close"/>
            </button>
          </div>
          <h1 className='menu__title'>
            Menu
          </h1>
          <div className='menu__links border-gradient' onClick={() => setShow(false)}>
            <Link to={OWERVIEW_ROUTE} className='menu__link'>overview</Link>
            <Link to={TRANSFERS_ROUTE} className='menu__link'>transfers</Link>
            <Link to={TRANSFERS_ROUTE + '/' + TRANSFERS_TOPUP_ROUTE} className='menu__link menu__link-inner'>top up</Link>
            <Link to={TRANSFERS_ROUTE + '/' + TRANSFERS_SEND_ROUTE} className='menu__link menu__link-inner'>send money</Link>
            <Link to={TRANSFERS_ROUTE + '/' + TRANSFERS_WITHDRAW_ROUTE} className='menu__link menu__link-inner'>withdraw</Link>
            <Link to={ARCHIVE_ROUTE + '/' + ARCHIVE_TRANSACTIONS_ROUTE} className='menu__link menu__link-last'>archive</Link>
          </div>
        </div>
      }    
    </>
  );
};

export default Menu;