import React, { useState } from 'react';
import { REGISTRATION_ROUTE } from '../../utils/consts';
import './Auth.scss';
import InputField from '../../components/InputField/InputField';
import { Link } from 'react-router-dom';
import userStore from '../../store/UserStore';
import { observer } from 'mobx-react-lite';
import { login } from '../../API/userAPI';
import Alert from '../../components/Alert/Alert';

const Login = observer(() => {
  const [username, setUsername] = useState(''); 
  const [password, setPassword] = useState('');
  const [showAlert, setShowAlert] = useState({
    show: false,
    message: ''
  });

  const auth = async (e) => {
    e.preventDefault();
    try {
      const user = await login(username, password);
      localStorage.setItem('user', JSON.stringify(user));
      userStore.setCurrentUser(user);
    } catch (e) {
      setShowAlert({show: true, message: e.response.data.detail});
      setTimeout(() => setShowAlert({show: false, message: e.response.data.detail}), 2000)
    }
  }

  return (
    <div className='container-auth'>
      <div className='auth'>
        <h1 className='auth__title'>
          Log In to Bank for people
        </h1>
        <p className='auth__subtitle'>
          New to Bank for people?  
          <Link to={REGISTRATION_ROUTE} className='auth__link'>Create an account</Link>
        </p>
        <form className='auth__form'>
          <InputField inputType='text'
                      placeholder='Enter your username'
                      titleName='Username'
                      onChange={(e) => setUsername(e.target.value)}
                      required/>
          <InputField inputType='password'
                      placeholder='Enter your password'
                      titleName='Password'
                      input='auth__input-password'
                      onChange={(e) => setPassword(e.target.value)}
                      required/>
          <Link to='/' className='auth__check'>Forgot password?</Link>
          <button disabled={password === '' || username === '' ? true : false } className='btn' onClick={auth}>Log In</button>
          {showAlert.show &&
            <Alert message={showAlert.message}/>
          }
        </form>
      </div>
    </div>
  );
});

export default Login;  